import React from 'react'
import './navbarBanner.css'
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { Link } from 'react-router-dom';

const NavbarBanner = () => {

const hanldeToggle = () => {
  const a = document.querySelector(".navbarBanner1");
  a.style.display = "block";
  
}
const handleClose = () => {
  const a = document.querySelector(".navbarBanner1");
  a.style.display="none";
}

  const options = [
    {
      "name" : "Fresh"
    },
    {
      "name" : "Amazon miniTV"
    },
    {
      "name" : "Sell"
    },
    {
      "name" : "Best Seller"
    },
    {
      "name" : "Today's Deals"
    },
    {
      "name" : "Mobiles"
    },
    {
      "name" : "Customer Service"
    },
    {
      "name" : "Prime"
    },
    {
      "name" : "Electronics"
    },
    {
      "name" : "Fashion"
    },
    {
      "name" : "Home & Kitchen"
    },
    {
      "name" : "New Releases"
    },
    {
      "name" : "Amazon Pay"
    },
    {
      "name" : "Computers"
    },
    {
      "name" : "Car & Motorbike"
    },
    {
      "name" : "Books"
    },
    
  ]

  return (
  <>
  <div className="navbarBanner">
    <div className="navbarBannerOptionsLeft">

      <div className="optionsNavbarBanner" onClick={hanldeToggle}>
        <MenuIcon sx={{fontSize:"26px"}} />
        <div className="allOptionsNavbarBanner" style={{fontWeight:"700"}}>All</div>
      </div>

    {
      options.map((item,index)=>{
        return(
          <Link to={'/products'} className="optionsNavbarBanner item" key={index}>
         <div className="allOptionsNavbarBanner">{item.name}</div>
           </Link>
        )
      })
    }
    </div>
  </div>
  <div className="navbarBanner1">
    <div className="navbarBannerOptionsLeft1">
    <div className="optionsNavbarBanner" onClick={handleClose}>
        <CloseIcon sx={{fontSize:"26px"}} />
        <div className="allOptionsNavbarBanner" style={{fontWeight:"700"}}>Close</div>
      </div>

    {
      options.map((item,index)=>{
        return(
          <Link to={'/products'} className="optionsNavbarBanner1" key={index}>
         <div className="allOptionsNavbarBanner1">{item.name}</div>
           </Link>
        )
      })
    }
    </div>
  </div>
  </>
  )
}

export default NavbarBanner;