import React,{useEffect,useState} from 'react'
import './cart.css'
import Footer from '../../Components/Footer/footer'
import { useSelector,useDispatch } from 'react-redux';
import { clearCart, removeFromCart } from '../../redux/actions/actions';
import { toast,ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from 'react-router-dom';



const Cart = () => {

    const [cartItem,setCartItem] = useState([]);
    const dispatch = useDispatch();
    const cartItems = useSelector((state)=> state.cart.items);

      

    let a = 0;
    let cost = cartItems.map((item)=>{ return a = a + item.price })

    useEffect(()=>{
        setCartItem(cartItems);
    },[cartItems])

    const handleRemoveFromCart =(id)=>{
        toast.error("Item Removed From Cart",{
            position:"bottom-right"
        }
        )
        dispatch(removeFromCart(id));
    }

    const Navigate = useNavigate()

    const handleBuyItem = (id) => {
   alert("Purchase Successfully.....")
   Navigate("/")
        dispatch(clearCart())
    }

  return (
    <>
    <div className="cart">

        <div className="topLeftCart">
                <div className="topLeftCartTitle">
                    Shopping Cart
                </div>
                <div className="deselectAllCart">Deselect all items</div>
                <div className="cartPriceTextDivider">Price</div>

                <div className="cartItemsDiv">


                {
                    cartItems.map((item,index)=>{
                        return(
                            <>
                            <div className="cartItemBlock">
                        <div className="cartItemLeftBlock">
                            <div className="cartItemLeftBlockImage">
                                <img src={item.img} alt="" className="cartItemLeftBlockImg" />
                            </div>
                            <div className="cartItemLeftBlockDetails">
                                <div className="cartItemProductName">
                                {item.discription}
                                </div>
                                <div className="inStockCart">In stock</div>
                                <div className="elgFreeShp">Elligible for Free Shipping</div>
                                <div className="amazonFullFilledImage">
                                    <img src="https://m.media-amazon.com/images/G/31/marketing/fba/fba-badge_18px._CB485936079_.png" alt="" className="fullfillImg" />
                                </div>
                                <div className="removeFromCart" onClick={()=>{handleRemoveFromCart(item.id)}}>
                                    Remove From Basket
                                </div>
                            </div>
                        </div>
                        <div className="cartItemRightBlock">
                            Rs {item.price}
                        </div>
                    </div>
                            </>
                        )
                    })
                }

                    
                </div>
        </div>

        <div className="topRightCart">

            <div className="subTotalTitle">Subtotal ({cartItem.length} items) : <span className='subTotalTitleSpan'>Rs {a}</span></div>
             <div className="giftAddto">
                <input type="checkbox" name="" id="" required />
                <div>This Order Contains a gift</div>
                </div>    
                <div onClick={handleBuyItem} className="proceedToBuy">
                    Proceed To Buy
                    </div>                             
        </div>

        <ToastContainer />

    </div>





    <Footer />
    </>
  )
};

export default Cart;